.container {
  max-width: 360px;
  width: 100%;
  .progress {
    border-radius: 30px;
    background-color: rgb(232, 232, 232);
    margin-bottom: 25px;
    height: 20px;
    .inner {
      height: 100%;
      border-radius: 30px;
      width: 10%;
      background: rgb(18, 231, 48);
      transition: all 0.3s ease-in-out;
      background: linear-gradient(
        90deg,
        rgba(18, 231, 48, 1) 0%,
        rgba(0, 212, 255, 1) 100%
      );
    }
  }
}
